import React from 'react';
import { useHistory } from "react-router-dom";
import { useSelector } from 'react-redux';
import styled from 'styled-components/macro';

import BlockSpinner from '../components/spinner/BlockSpinner';

export default function Welcome() {
  const history = useHistory();

  const { status } = useSelector(state => state.root);

  return (
    <Container>
      {
        status === 'loading' ?
        <BlockSpinner /> :
        <BtnStart onClick={() => history.push('/start')}>Let's Start</BtnStart>
      }
    </Container>
  )
}


const Container = styled.div.attrs({ className: 'flex items-center justify-center'})`
  min-height: 250px;
`;

const BtnStart = styled.button.attrs({ className: 'button'})`
  --button-font-size: 17px;
  --button-font-weight: normal;
  --button-padding: 5px 12px;
`;
