import React, { useEffect } from 'react';
import { MemoryRouter, BrowserRouter, Route } from "react-router-dom";
import { useDispatch } from 'react-redux';

// screens
import Steps from './screens/Steps';
import Welcome from './screens/Welcome';
import Alert from './alert/Alert';

import { loadAppData } from './services/slices/rootSlice';

export default function App({ initid }) {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(loadAppData(`${initid}`));
  }, [dispatch, initid]);

  return (
    <>
      <BrowserRouter>
        <main>
          <MemoryRouter>
            <Route exact path="/" component={Welcome} />
            <Route exact path="/start" component={Steps} />
          </MemoryRouter>
        </main>
        <Alert />
      </BrowserRouter>
    </>
  );
}
